import React from "react";
import { useForm } from "../../hooks/useForm";
import { controls } from "../../components/Inputs";
import { Box, Button } from "@material-ui/core";
import { GetTextData } from "../Server";

export default ({ value, addorEdit,view }) => {
  const initialValues = { projectCode: "", projectName: "", description: "" };
  const { values, handleInputChange, resetForm,setValue } = useForm(
    Object.keys(value).length == 0 ? initialValues : value,
    false,
    {}
  );

  const handleSubmit = (e) => {
    e.preventDefault();
    addorEdit(values);
    resetForm(initialValues);
  };

  const generateProjectCode=()=>{
    GetTextData('/project/genarateProjectCode').then(d=>setValue("projectCode",d))
    
  }
  return (
    <form onSubmit={handleSubmit}>
      <Box display="flex" mb={2}>
        <Box flex={1}>
          <controls.MyInput1
            label="projectCode*"
            name={"projectCode"}
            disabled
            required
            value={values?.projectCode}
            onChange={handleInputChange}
          />
        </Box>
       {(!view  ) && <Button variant="contained" disabled={Object.keys(value).length != 0} color="primary" size="small" onClick={generateProjectCode}>
          Generate Code
        </Button>}
      </Box>
      <controls.MyInput1
        label="projectName*"
        name={"projectName"}
        disabled={view}
        required
        value={values?.projectName}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        multiline
        rows={4}
        label="description"
        name={"description"}
        disabled={view}
        value={values?.description}
        onChange={handleInputChange}
      />
      {!view &&
      <Box textAlign="center">
        <controls.Submit text={"submit"} />{" "}
      </Box>}
    </form>
  );
};
