import React, { useState } from 'react'
import SimpleTabs from '../../components/tabs/Mytabs'
import MyTable from '../../components/tables/MyTable'
import makeData from '../../components/tables/makeData'
import { usePopup } from '../../hooks/usePopup';
import { useConfirmation } from '../../hooks/useConfirmation';
import { DeleteButton, EditButton, ViewButton } from '../../components/buttons/AddButton'
import SuppliersForm from './SuppliersForm';
import { useSuppliersService } from './SuppliersService';

export default ()=> {
    const label1='List'
    const label2='Add Suppliers'
    const MyForm=SuppliersForm
    const [mydata,actions]=useSuppliersService()
    const [view,setView]=useState(false)
    const {MyPopup,openDialog}=usePopup(view ?'View ':'Edit Suppliers');
    const {ConfirmPopup,openConfirm}=useConfirmation('Are you sure','once deleted not recoverd');
    const [value,setvalue]=useState({})
    const addorEdit=(v)=>{
    if(Object.keys(value).length>0){actions?.update(v); openDialog()}
    
    else actions?.add(v)
      
      setvalue({})
    }
    const data=React.useMemo(()=>{
        return mydata;
    },[mydata])
    const columns = React.useMemo(
        () => [
         
              {
                Header: "First Name",
                accessor: "firstName"
              },
              {
                Header: "Last Name",
                accessor: "lastName"
          },
          {
            Header: "Email",
            accessor: "email"
          },
          {
            Header: "Phone",
            accessor: "phone"
      },
      {
        Header: "vendorCode",
        accessor: "vendorCode"
  },
      {
        Header: "Gstin",
        accessor: "gstin"
  },
      {
        Header: "Primary Address",
        accessor: "primaryAddress"
      },
     
              {
                Header: "Actions",
                Cell: ({ row }) => (
                
                   <>
                  <ViewButton
                     
                     onClick={()=>{setView(true);setvalue(row.original);openDialog()}}
                    />
                     
                    <EditButton
                     
                     onClick={()=>{setView(false);setvalue(row.original);openDialog()}}
                    />
                     
                    <DeleteButton 
                      onClick={() => openConfirm(()=>actions?.deletec(row.original))}
                    />
                  </>
                 
                )
              }
            ]
          ,
        []
      );
    
    return (<>
        <SimpleTabs label1={label1} label2={label2} form={<MyForm value={value} addorEdit={addorEdit}/>} table={<MyTable columns={columns} data={data}/>}/>
        <MyPopup  maxWidth='sm' fullWidth><MyForm view={view} value={value} addorEdit={addorEdit} /></MyPopup>
        <ConfirmPopup/>
        </>
    )
}
