import React from "react";
import { useForm } from "../../hooks/useForm";
import { controls } from "../../components/Inputs";
import { Box, Button } from "@material-ui/core";

export default ({ value, addorEdit,view }) => {
  const initialValues = {
    firstName: "",
    lastName: "",
    email: "",
    phone: "",
    password: "",
    primaryAddress: "",
    secondryAddress2: "",
    secondryAddress3: "",
    secondryAddress4: "",
    vendorCode: "",
    vendorImage: "",
    code: 0,
    gstin: 0,
    gst: 0,
  };
  const { values, handleInputChange, resetForm } = useForm(
    Object.keys(value).length == 0 ? initialValues : value,
    false,
    {}
  );

  const handleSubmit = (e) => {
    e.preventDefault();
    addorEdit(values);
    resetForm(initialValues);
  };
  return (
    <form onSubmit={handleSubmit}>
      <controls.MyInput1
        label="firstName"
        name={"firstName"}
        disabled={view}
        required
        value={values?.firstName}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="lastName"
        name={"lastName"}
        required
        disabled={view}
        value={values?.lastName}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="email"
        name={"email"}
        type="email"
        required
        disabled={view}
        value={values?.email}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="phone"
        name={"phone"}
        required
        disabled={view}
        value={values?.phone}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="gstin"
        name={"gstin"}
        required
        disabled={view}
        value={values?.gstin}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="password"
        type="password"
        required
        disabled={view}
        name={"password"}
        value={values?.password}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="primaryAddress"
        name={"primaryAddress"}
        disabled={view}
        required
        value={values?.primaryAddress}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="secondryAddress2"
        name={"secondryAddress2"}
        disabled={view}
        required
        value={values?.secondryAddress2}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="secondryAddress3"
        name={"secondryAddress3"}
        disabled={view}
        required
        value={values?.secondryAddress3}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="vendorCode"
        name={"vendorCode"}
        disabled={view}
        required
        value={values?.vendorCode}
        onChange={handleInputChange}
      />
      <Box textAlign="center">
      {!view &&  <controls.Submit text={"submit"} />}
      </Box>
    </form>
  );
};
