import React from "react";
import { useForm } from "../../hooks/useForm";
import { controls } from "../../components/Inputs";
import { Box, Button } from "@material-ui/core";

export default ({ value, addorEdit, roles: myroles,view }) => {
  const initialValues = {
    name: "",
    email: "",
    password: "",
    address: "",
    role: [],
  };
  const { values, handleInputChange, resetForm, setValue } = useForm(
    Object.keys(value).length == 0 ? initialValues : value,
    false,
    {}
  );
  const roleOptions = [
    { value: "admin", label: "Admin", id: "1" },
    { value: "siteengineer", label: "Site Engineer", id: "2" },
    { value: "store", label: "Store", id: "3" },
  ];

  const handleSubmit = (e) => {
    e.preventDefault();
    //alert(JSON.stringify(values))
    addorEdit(values);
    resetForm(initialValues);
  };
  return (
    <form onSubmit={handleSubmit}>
      <controls.MyInput1
        label="name"
        name={"name"}
        disabled={view}
        required
        value={values?.name}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="email"
        name={"email"}
        disabled={view}
        required
        value={values?.email}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="password"
        name={"password"}
        type='password'
        disabled={view}
        required
        value={values?.password}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="address"
        name={"address"}
        disabled={view}
        required
        value={values?.address}
        onChange={handleInputChange}
      />
      <controls.MySelect
        label="Roles"
        value={(values?.role||[]).map(v=>({...v,label:v.name,value:v.name}))}
        options={myroles}
        isMulti={true}
        disabled={view}
        required
        getOptionLabel={(option) => option.name}
        getOptionValue={(option) => option.name}
        placeholder={values?.materialTypes || "Select Roles"}
        onChange={(v) => setValue("role", v)}
      />
      <Box textAlign="center">
       {!view && <controls.Submit text={"submit"} />} 
      </Box>
    </form>
  );
};
