import React, { useState } from 'react'
import SimpleTabs from '../../components/tabs/Mytabs'
import MyTable from '../../components/tables/MyTable'
import makeData from '../../components/tables/makeData'
import { usePopup } from '../../hooks/usePopup';
import { useConfirmation } from '../../hooks/useConfirmation';
import { DeleteButton, EditButton, ViewButton } from '../../components/buttons/AddButton'
import ItemForm from './ItemForm';
import { useItemService } from './ItemService';
import useFetch from '../../hooks/useFetch';

export default ()=> {
  const [loading,error,itemtypes]=useFetch('ItemType/getAllitemType');
  const [uomloading,uomerror,uomList]=useFetch('Uom/getAllUoms');
    const label1='List'
    const label2='Add Item'
    const MyForm=ItemForm
    const [mydata,actions]=useItemService()
    const [view,setView]=useState(false)
    const {MyPopup,openDialog}=usePopup(view ?'View ':'Edit Items');
    const {ConfirmPopup,openConfirm}=useConfirmation('Are you sure','once deleted not recoverd');
    const [value,setvalue]=useState({})
    const addorEdit=(v)=>{
    if(Object.keys(value).length>0){actions?.update(v); openDialog()}
    
    else actions?.add(v)
      
      setvalue({})
    }
    const data=React.useMemo(()=>{
        return mydata;
    },[mydata])
    const columns = React.useMemo(
        () => [
         
              {
                Header: "Item Code",
                accessor: "materialCode"
              },
              {
                Header: "Item Name",
                accessor: "materialDescription"
          },
          {
            Header: "Item Type",
            accessor: "materialTypes"
          },
          {
            Header: "UOM",
            accessor: "uom"
      },
      {
        Header: "HSN Code",
        accessor: "hsnCode"
      },
      {
        Header: "Specifications",
        accessor: "materialSpecification",
        Cell: ({ row }) => (<a href={row.original.materialSpecification} target='_blank'>view</a>)
  },
         
              {
                Header: "Actions",
                Cell: ({ row }) => (
                  <>
                  <ViewButton
                     
                     onClick={()=>{openDialog()}}
                    />
                     
                    <EditButton
                     
                     onClick={()=>{setvalue(row.original);openDialog()}}
                    />
                     
                    <DeleteButton 
                      onClick={() => openConfirm(()=>actions?.deletec(row.original))}
                    />
                  </>
                )
              }
            ]
          ,
        []
      );
    
    return (<>
        <SimpleTabs label1={label1} label2={label2} form={<MyForm value={value} view={view} uomList={uomList} itemtypes={itemtypes} addorEdit={addorEdit}/>} table={<MyTable columns={columns} data={data}/>}/>
        <MyPopup  maxWidth='sm' fullWidth><MyForm itemtypes={itemtypes} view={view} uomList={uomList} value={value} addorEdit={addorEdit} /></MyPopup>
        <ConfirmPopup/>
        </>
    )
}
