import React from "react";
import { useForm } from "../../hooks/useForm";
import { controls } from "../../components/Inputs";
import { Box, Button } from "@material-ui/core";
import useFetch from "../../hooks/useFetch";
import { GetTextData } from "../Server";

export default ({ value, addorEdit, itemtypes: data, uomList ,view}) => {
  const initialValues = {
    hsnCode: 0,
    file: "",
    itemCode: "",
    materialCode: "",
    materialDescription: "",
    materialNumbers: 0,
    materialSpecification: "",
    materialTypes: "",
    uom: "",
  };
  const { values, handleInputChange, resetForm, setValue,setValues } = useForm(
    Object.keys(value).length == 0 ? initialValues : value,
    false,
    {}
  );

  const handleSubmit = (e) => {
    e.preventDefault();
   // alert(JSON.stringify(values));
     addorEdit(values)
    resetForm(initialValues);
  };
  const getProjectCode = (v) => {
    //    alert(v)
    const { itemTypeName } = v;
    //  const e={}
    //  e.target={name:'materialTypes',value:itemTypeName}

    //  alert(JSON.stringify(e))
    //handleInputChange({e:{target:{name:'materialTypes',value:itemTypeName}}})
    setValue("materialTypes", "itemTypeName");
    GetTextData('/AdminMaterialDescription/genarateMtdCodes/'+itemTypeName).then(d=>{setValues(c=>({...c,materialTypes:itemTypeName,materialCode:d}))})
    //setProjectCode(v)
  };
  //  const setProjectCode=(v)=>GetTextData('/AdminMaterialDescription/genarateMtdCodes/'+v.itemTypeName).then(d=>{setValue('itemCode',d)})
  const setUom = (v) => {
    setValue("uom", v.units);
  };
  return (
    <form onSubmit={handleSubmit}>
      <controls.MyInput1
        label="item code"
        required
        disabled
        name={"materialCode"}
        value={values?.materialCode}
        onChange={handleInputChange}
      />
      <controls.MyInput1
        label="itemName"
        required
        disabled={view}
        name={"materialDescription"}
        value={values?.materialDescription}
        onChange={handleInputChange}
      />
      <controls.MySelect
        label="uom"
        required
        name={"uom"}
        disabled={view}
        options={uomList}
        getOptionLabel={(option) => option.units}
        getOptionValue={(option) => option.units}
        placeholder={values?.uom || "Select Uom"}
        onChange={setUom}
      />
      <controls.MyInput1
        label="hsncode"
        name={"hsnCode"}
        required
        disabled={view}
        value={values?.hsnCode}
        onChange={handleInputChange}
      />
      <controls.MySelect
        label="ItemType"
        name={"materialTypes"}
        options={data}
        getOptionLabel={(option) => option.itemTypeName}
        getOptionValue={(option) => option.itemTypeName}
        placeholder={values?.materialTypes || "Select Item Type"}
        onChange={(v) => {
          getProjectCode(v);
        }}
      />

      <controls.MyInput1
        label="specifications"
        disabled={view} 
        required
        type="file"
        name={"file"}
        onChange={handleInputChange}
      />

      <Box textAlign="center">
        <controls.Submit text={"submit"} />{" "}
      </Box>
    </form>
  );
};
