import React from "react";
import { useForm } from "../../hooks/useForm";
import { controls } from "../../components/Inputs";
import { Box, Button } from "@material-ui/core";

export default ({ value, addorEdit ,fields}) => {
  const initialValues = { manageFildsName: "", manageFieldValues: "" };
  const { values, handleInputChange, resetForm ,setValue} = useForm(
    Object.keys(value).length == 0 ? initialValues : value,
    false,
    {}
  );

  const handleSubmit = (e) => {
    e.preventDefault();
    addorEdit(values);
    resetForm(initialValues);
  };
  return (
    <form onSubmit={handleSubmit}>
      <controls.MySelect
        label="Fields"
        required
        name={"manageFildsName"}
        options={fields}
        getOptionLabel={(option) => option.manageFildsName}
        getOptionValue={(option) => option.manageFildsMasterId}
        placeholder={values?.manageFildsMasterId || "Select Fields"}
        onChange={(v)=>setValue("manageFildsMasterId",v.manageFildsMasterId)}
      />
      <controls.MyInput1
        label="value"
        required
        name={"manageFieldValues"}
        value={values?.manageFieldValues}
        onChange={handleInputChange}
      />
      <Box textAlign="center">
        <controls.Submit text={"submit"} />{" "}
      </Box>
    </form>
  );
};
