import React, { useEffect, useReducer, useState } from 'react'
import useLocalStorage from '../../hooks/useLocalStorage';
import { toast } from 'react-toastify';
import { Getdata, Postdata,Deletedata } from '../Server';
export const useUserService=()=>{
    
    const [data,setData]=useState([]);
   
    useEffect(()=>{
Getdata('/uuser/getAll').then(d=>setData(d))
    },[])
    const add=(v)=>{
        Postdata("/uuser/registrations","POST",v).then(d=>{
            if(d){
                toast.success("Submitted Successfully")
                setData(data=>[...data,d])
            }
            else{
                toast.error("Somting went wrong")
            }
           
        })
       
    }

    const update=(v)=>{
        Postdata("/uuser/update/"+v.userId,"PUT",v).then(d=>{
            if(d){
                if(d.status==0){
                    toast.error(d.message)
                }
                else{
                toast.success("Updated Successfully")
                setData(data=>data.map(item1=>item1.userId==v.userId?v:item1))}
            }
            else{
                toast.error("Somting went wrong")
            }
        })
     
    }
    
    const deletec=(v)=>{
        
        Deletedata("/uuser/user/"+v.userId,"DELETE",{}).then(d=>{
            toast.error("deleted Successfully")
      
       setData(data=>data.filter(item1=>item1.userId!=v.userId))
        })
       
        }
 const actions={add,update,deletec}
return [data,actions]
}