import React, { useState } from "react";
import SimpleTabs from "../../components/tabs/Mytabs";
import MyTable from "../../components/tables/MyTable";
import makeData from "../../components/tables/makeData";
import { usePopup } from "../../hooks/usePopup";
import { useConfirmation } from "../../hooks/useConfirmation";
import {
  DeleteButton,
  EditButton,
  ViewButton,
} from "../../components/buttons/AddButton";
import UserForm from "./UsersForm";
import { useUserService } from "./UserService";
import useFetch from "../../hooks/useFetch";

export default () => {
  const label1 = "List";
  const label2 = "Add User";
  const [loading, error, roles] = useFetch("Role/getAllRoles");
  const MyForm = UserForm;
  const [mydata, actions] = useUserService();
  const [view,setView]=useState(false)
  const {MyPopup,openDialog}=usePopup(view ?'View ':'Edit Users');
  const { ConfirmPopup, openConfirm } = useConfirmation(
    "Are you sure",
    "once deleted not recoverd"
  );
  const [value, setvalue] = useState({});
  const addorEdit = (v) => {
    if (Object.keys(value).length > 0) {
      actions?.update(v);
      openDialog();
    } else actions?.add(v);

    setvalue({});
  };
  const data = React.useMemo(() => {
    return mydata;
  }, [mydata]);
  const columns = React.useMemo(
    () => [
      {
        Header: " Name",
        accessor: "name",
      },
      {
        Header: "Email",
        accessor: "email",
      },
      {
        Header: "Roles",
        Cell:({ row }) =>(<>{row?.original?.role?.map(({name})=>name).join(",")}</>)
      },
      {
        Header: "Address",
        accessor: "address",
      },

      {
        Header: "Actions",
        Cell: ({ row }) => (
          <>
            <ViewButton
              onClick={() => {
                setView(true)
                setvalue(row.original);
                openDialog();
              }}
            />

            <EditButton
              onClick={() => {
                setView(false)
                setvalue(row.original);
                openDialog();
              }}
            />

            <DeleteButton
              onClick={() => openConfirm(() => actions?.deletec(row.original))}
            />
          </>
        ),
      },
    ],
    []
  );

  return (
    <>
      <SimpleTabs
        label1={label1}
        label2={label2}
        form={<MyForm value={value} roles={roles} addorEdit={addorEdit} />}
        table={<MyTable columns={columns} data={data} />}
      />
      <MyPopup maxWidth="sm" fullWidth>
        <MyForm view={view} roles={roles} value={value} addorEdit={addorEdit} />
      </MyPopup>
      <ConfirmPopup />
    </>
  );
};
