import React, { useEffect, useReducer, useState } from 'react'
import useLocalStorage from '../../hooks/useLocalStorage';
import { toast } from 'react-toastify';
import useFetch from '../../hooks/useFetch';
import { Getdata, Postdata,Deletedata } from '../Server';
export const useProjectsService=()=>{
    
    const [data,setData]=useState([]);
   
    useEffect(()=>{
Getdata('/project/getAll').then(d=>setData(d))
    },[])
    const add=(v)=>{
        Postdata("/project/add","POST",v).then(d=>{
            toast.success("Submitted Successfully")
            setData(data=>[...data,d])
        })
       
    }

    const update=(v)=>{
        Postdata("/project/add","POST",v).then(d=>{
            toast.success("Updated Successfully")
            setData(data=>data.map(item1=>item1.id==v.id?v:item1))
        })
     
    }
    
    const deletec=(v)=>{
        
        Deletedata("/project/user/"+v.id,"DELETE",{}).then(d=>{
            toast.error("deleted Successfully")
      
       setData(data=>data.filter(item1=>item1.id!=v.id))
        })
       
        }
 const actions={add,update,deletec}
return [data,actions]
}